import type Cube from '@/cube/model/Cube';
import type Face from '@/cube/model/Face';

export default class CubeDataPoints {
    private static readonly CENTER_PIECE: number = 4;

    public static tuples(cube: Cube): number[] {
        return cube.faces.flatMap((face: Face) => {
            return face.colours
                .flat()
                .map((colour: number, index: number) => {
                    if (index == CubeDataPoints.CENTER_PIECE) {
                        return [];
                    }
                    const nodes = [0, 0, 0, 0, 0, 0];
                    nodes[colour - 1] = 1;
                    return nodes;
                })
                .flat();
        });
    }
}
