import type Face from '@/cube/model/Face';

export default class FaceHelper {
    public static newFace(colour?: number): Face {
        const colours: number[][] = [];
        if (colour) {
            for (let i = 0; i < 3; i++) {
                colours.push([colour, colour, colour]);
            }
        }
        return {
            colours,
        };
    }

    public static cloneFace(face: Face): Face {
        const colours: number[][] = [];
        face.colours.forEach((row: number[]) => colours.push([...row]));
        return FaceHelper.faceWith(colours);
    }

    public static faceWith(colours: number[][]): Face {
        const face = FaceHelper.newFace();
        face.colours = colours;
        return face;
    }
}

export const { newFace, faceWith, cloneFace } = FaceHelper;
