import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import type CubeState from '@/state/CubeState';
import { newCube } from '@/cube/model/CubeHelper';
import CubeController from '@/cube/CubeController';
import CubePlayer from '@/cube/CubePlayer';
import nodeWeights from '@/neuralnet/NeuralNetworkState.json';
import NeuralNetwork from '@/neuralnet/NeuralNetwork';

const initialState: CubeState = {
    nodeWeights: { weights: nodeWeights },
    cube: newCube(),
    running: false,
    xRotation: 315,
    yRotation: 315,
    difficulty: 8,
    moves: 0,
    complete: true,
    transition: 0,
    sameMoveCount: 0,
};

const cubeSlice = createSlice({
    name: 'cube',
    initialState: initialState,
    reducers: {
        start(state) {
            if (!state.complete) {
                state.running = true;
            }
        },
        stop(state) {
            state.running = false;
        },
        rotateX(state, action: PayloadAction<number>) {
            state.xRotation = action.payload;
        },
        rotateY(state, action: PayloadAction<number>) {
            state.yRotation = action.payload;
        },
        setDifficulty(state, action: PayloadAction<number>) {
            state.difficulty = action.payload;
        },
        move(state) {
            if (state.running) {
                const neuralNetwork = new NeuralNetwork(state.nodeWeights);
                const cubeStatus = CubePlayer.move(state.cube, neuralNetwork);
                state.moves = state.moves + 1;
                state.complete = cubeStatus.completed == 1;
                state.sameMoveCount = cubeStatus.lastMove == state.lastMove ? state.sameMoveCount + 1 : 0;
                state.lastMove = cubeStatus.lastMove;
                state.running = !state.complete && state.sameMoveCount < 4;
                state.transition = 80;
            }
        },
        transitionStep(state) {
            if (state.transition > 0) {
                state.transition = state.transition - 10;
            }
        },
        reset(state) {
            state.cube = CubeController.createMuddledCube(state.difficulty);
            state.running = false;
            state.moves = 0;
            state.complete = false;
            state.transition = 0;
            state.lastMove = undefined;
            state.sameMoveCount = 0;
        },
    },
});

export const { stop, start, rotateX, rotateY, setDifficulty, move, reset, transitionStep } = cubeSlice.actions;
export default cubeSlice.reducer;
