export default class Randomiser {
    private seed: number;

    private readonly throwAwayFactor = 10000;

    constructor(seed?: number) {
        this.seed = seed ? seed : Math.floor(Math.random() * 100) + 100;
    }

    /**
     * Returns a pseudo-random choice between 0 but less than n
     * @param choices The maximum choice number n
     */
    public randomChoice(choices: number): number {
        let random = Math.sin(this.seed++) * this.throwAwayFactor;
        random = random - Math.floor(random);
        return Math.floor(random * choices);
    }
}
