import type Cube from '@/cube/model/Cube';
import Randomiser from '@/util/Randomiser';
import {
    newCube,
    rotateX1,
    rotateX3,
    rotateY1,
    rotateY3,
    rotateZ1,
    rotateZ3,
    reverseRotateX1,
    reverseRotateX3,
    reverseRotateY1,
    reverseRotateY3,
    reverseRotateZ1,
    reverseRotateZ3,
} from '@/cube/model/CubeHelper';
import {
    PLANE_X1,
    PLANE_X3,
    PLANE_Y1,
    PLANE_Y3,
    PLANE_Z1,
    PLANE_Z3,
    REV_PLANE_X1,
    REV_PLANE_X3,
    REV_PLANE_Y1,
    REV_PLANE_Y3,
    REV_PLANE_Z1,
    REV_PLANE_Z3,
} from '@/cube/Move';

export default class CubeController {
    private static readonly MOVES_AVAILABLE = 6;

    public static createMuddledCube(moves: number, randomSeed?: number): Cube {
        const randomiser = new Randomiser(randomSeed);
        const cube = newCube();

        for (let i = 0; i < moves; i++) {
            CubeController.applyReverseAction(cube, randomiser.randomChoice(CubeController.MOVES_AVAILABLE));
        }

        return cube;
    }

    public static applyReverseAction(cube: Cube, action: number) {
        CubeController.applyAction(cube, action + 6);
    }

    public static applyAction(cube: Cube, action: number) {
        switch (action) {
            case PLANE_X1:
                rotateX1(cube);
                break;
            case PLANE_X3:
                rotateX3(cube);
                break;
            case PLANE_Y1:
                rotateY1(cube);
                break;
            case PLANE_Y3:
                rotateY3(cube);
                break;
            case PLANE_Z1:
                rotateZ1(cube);
                break;
            case PLANE_Z3:
                rotateZ3(cube);
                break;
            case REV_PLANE_X1:
                reverseRotateX1(cube);
                break;
            case REV_PLANE_X3:
                reverseRotateX3(cube);
                break;
            case REV_PLANE_Y1:
                reverseRotateY1(cube);
                break;
            case REV_PLANE_Y3:
                reverseRotateY3(cube);
                break;
            case REV_PLANE_Z1:
                reverseRotateZ1(cube);
                break;
            case REV_PLANE_Z3:
                reverseRotateZ3(cube);
                break;
            default:
                break;
        }
    }
}
