import React, { ChangeEvent } from 'react';
import { Col, Container, Row, Input } from 'reactstrap';
import { connect, ConnectedProps } from 'react-redux';
import { start, stop, rotateX, rotateY, setDifficulty, reset, move } from '@/state/CubeSlice';
import { RootState } from '@/state/Store';

interface ControlPanelState {
    speed: number;
}

export class ControlPanel extends React.Component<PropsFromRedux, ControlPanelState> {
    private readonly MIN_SPEED = 1;
    private readonly MAX_SPEED = 10;
    private readonly MIN_ANGLE = 0;
    private readonly MAX_ANGLE = 360;
    private readonly MIN_DIFFICULTY = 1;
    private readonly MAX_DIFFICULTY = 20;
    private intervalId: NodeJS.Timeout | undefined = undefined;

    constructor(props: PropsFromRedux) {
        super(props);
        this.state = {
            speed: this.MIN_SPEED,
        };
    }

    fireEvent() {
        if (this.props.running) {
            this.props.move();
        }
        this.startTimer();
    }

    componentDidMount() {
        this.startTimer();
    }

    componentWillUnmount() {
        this.stopTimer();
    }

    startTimer() {
        const intervalTime = (this.MAX_SPEED - this.state.speed + 1) * 150;
        this.intervalId = setTimeout(() => this.fireEvent(), intervalTime);
    }

    stopTimer() {
        if (this.intervalId) {
            clearTimeout(this.intervalId);
            this.intervalId = undefined;
        }
    }

    setTimer(event: ChangeEvent<HTMLInputElement>) {
        const speed = parseInt(event.target.value);
        this.setState({ speed });
        this.stopTimer();
        this.startTimer();
    }

    render() {
        return (
            <Container fluid={true} data-testid="controlPanel">
                <Row className="vcenter">
                    <Col lg={{ size: 11, offset: 1 }} className="h2 text-center">
                        Control Panel
                    </Col>
                </Row>
                <Row className="vcenter">
                    <Col lg={{ size: 1, offset: 1 }} className="h2 text-center">
                        <i className={'bi bi-arrow-down-up fs-4'}></i>
                    </Col>
                    <Col lg={9} className="h2 text-center">
                        <Input
                            aria-label="xRotation"
                            type="range"
                            min={this.MIN_ANGLE}
                            max={this.MAX_ANGLE}
                            value={this.props.xRotation}
                            step={10}
                            onChange={(event) => this.props.rotateX(event.target.valueAsNumber)}
                        />
                    </Col>
                </Row>
                <Row className="vcenter">
                    <Col lg={{ size: 1, offset: 1 }} className="h2 text-center">
                        <i className={'bi bi-arrow-left-right fs-4'}></i>
                    </Col>
                    <Col lg={9} className="h2 text-center">
                        <Input
                            aria-label="yRotation"
                            type="range"
                            min={this.MIN_ANGLE}
                            max={this.MAX_ANGLE}
                            value={this.props.yRotation}
                            step={10}
                            onChange={(event) => this.props.rotateY(event.target.valueAsNumber)}
                        />
                    </Col>
                </Row>
                <Row className="vcenter">
                    <Col lg={{ size: 1, offset: 1 }} className="h2 text-center">
                        <i className={'bi bi-shuffle fs-4'}></i>
                    </Col>
                    <Col lg={9} className="h2 text-center">
                        <Input
                            aria-label="difficulty"
                            type="range"
                            min={this.MIN_DIFFICULTY}
                            max={this.MAX_DIFFICULTY}
                            value={this.props.difficulty}
                            step={1}
                            onChange={(event) => this.props.setDifficulty(event.target.valueAsNumber)}
                        />
                    </Col>
                </Row>
                <Row className="vcenter">
                    <Col lg={{ size: 1, offset: 1 }} className="h2 text-center">
                        <i className={'bi bi-speedometer2 fs-4'}></i>
                    </Col>
                    <Col lg={9} className="h2 text-center">
                        <Input
                            aria-label="speed"
                            type="range"
                            min={this.MIN_SPEED}
                            max={this.MAX_SPEED}
                            value={this.state.speed}
                            onChange={this.setTimer.bind(this)}
                        />
                    </Col>
                </Row>
                <Row className="vcenter">
                    <Col lg={{ size: 5, offset: 1 }} className="h2 text-center">
                        <button
                            key="eject"
                            aria-label="muddle"
                            type="button"
                            className="btn btn-outline-danger"
                            onClick={() => this.props.reset()}
                        >
                            <b>Muddle</b>
                        </button>
                    </Col>
                    {this.props.running && (
                        <Col lg={6} className="h2 text-center">
                            <button
                                aria-label="stop"
                                type="button"
                                className="btn btn-outline-success"
                                onClick={() => this.props.stop()}
                            >
                                <b>Stop</b>
                            </button>
                        </Col>
                    )}
                    {!this.props.running && (
                        <Col lg={6} className="h2 text-center">
                            <button
                                aria-label="solve"
                                type="button"
                                className="btn btn-outline-success"
                                onClick={() => this.props.start()}
                            >
                                <b>Solve</b>
                            </button>
                        </Col>
                    )}
                </Row>
            </Container>
        );
    }
}

const mapStateToProps = (state: RootState) => ({
    running: state.cube.running,
    xRotation: state.cube.xRotation,
    yRotation: state.cube.yRotation,
    difficulty: state.cube.difficulty,
});

const connector = connect(mapStateToProps, { start, stop, rotateX, rotateY, setDifficulty, move, reset });
type PropsFromRedux = ConnectedProps<typeof connector>;

export default connector(ControlPanel);
