import type Evaluation from '@/neuralnet/Evaluation';
import { Matrix } from 'ml-matrix';
import NodeWeights from '@/neuralnet/NodeWeights';

export default class NeuralNetwork {
    private readonly hiddenWeightMatrix: Matrix[];
    private readonly LEARN_RATE: number = 0.1;

    constructor(nodeWeights?: NodeWeights) {
        this.hiddenWeightMatrix = [];
        if (nodeWeights) {
            this.loadSavedState(nodeWeights.weights);
        }
    }

    private loadSavedState(weights: number[][][]) {
        weights.forEach((val) => this.hiddenWeightMatrix.push(new Matrix(val)));
    }

    public initialiseWeights(inputNodes: number, hiddenNodes: number, hiddenLayers: number, outputNodes: number) {
        const inputMatrix = new Matrix(inputNodes + 1, hiddenNodes);
        this.initialiseMatrix(inputMatrix);
        this.hiddenWeightMatrix.push(inputMatrix);

        for (let index = 1; index <= hiddenLayers; index++) {
            const outputs = index == hiddenLayers ? outputNodes : hiddenNodes;
            const hiddenMatrix = new Matrix(hiddenNodes + 1, outputs);
            this.initialiseMatrix(hiddenMatrix);
            this.hiddenWeightMatrix.push(hiddenMatrix);
        }
    }

    // Feed forward
    public evaluate(input: number[]): Evaluation {
        const output: number[][] = [];
        output.push(this.nodeOutput(this.toMatrixWithBias(input), this.hiddenWeightMatrix[0]));
        for (let index = 1; index <= this.hiddenWeightMatrix.length - 1; index++) {
            output.push(this.nodeOutput(this.toMatrixWithBias(output[index - 1]), this.hiddenWeightMatrix[index]));
        }

        return {
            triggeredNode: output[output.length - 1].indexOf(Math.max(...output[output.length - 1])),
            output,
        };
    }

    private toMatrixWithBias(input: number[]) {
        return new Matrix([[...input, 1]]);
    }

    private nodeOutput(inputMatrix: Matrix, weightMatrix: Matrix) {
        return inputMatrix
            .mmul(weightMatrix)
            .getRow(0)
            .map((value: number) => this.sigmoid(value));
    }

    private sigmoid(value: number): number {
        return 1 / (1 + Math.exp(-value));
    }

    private sigmoidDerivative(value: number): number {
        return value * (1 - value);
    }

    public applyFeedback(input: number[], expectedNode: number) {
        const evaluation: Evaluation = this.evaluate(input);
        const inputs: number[][] = [input, ...evaluation.output];
        const outputs = this.hiddenWeightMatrix[this.hiddenWeightMatrix.length - 1].columns;
        const expectedOutput: Matrix = Matrix.zeros(1, outputs).set(0, expectedNode, 1);
        let outputError: Matrix | undefined;

        for (let layer = this.hiddenWeightMatrix.length - 1; layer >= 0; layer--) {
            // calc layer error
            const output: number[] = evaluation.output[layer];
            const layerInput = inputs[layer];

            if (outputError) {
                outputError = this.hiddenWeightMatrix[layer + 1].mmul(outputError.transpose()).transpose();
                outputError.removeColumn(outputError.columns - 1);
            } else {
                outputError = Matrix.sub(expectedOutput, new Matrix([output]));
            }

            this.adjustWeighting(output, outputError, layerInput, this.hiddenWeightMatrix[layer]);
        }
    }

    private adjustWeighting(output: number[], outputError: Matrix, input: number[], weightMatrix: Matrix) {
        // calculate gradient
        const outputsDerivative: Matrix = new Matrix([output.map((value: number) => this.sigmoidDerivative(value))]);
        const gradient: Matrix = outputError.mul(outputsDerivative).mul(this.LEARN_RATE);

        // calculate the delta
        const weightDelta: Matrix = this.toMatrixWithBias(input).transpose().mmul(gradient);
        weightMatrix.add(weightDelta);
    }

    private initialiseMatrix(matrix: Matrix) {
        const upper = -1 / Math.sqrt(matrix.rows);
        const lower = 1 / Math.sqrt(matrix.rows);

        for (let i = 0; i < matrix.rows; i++) {
            for (let j = 0; j < matrix.columns; j++) {
                const node = j * i;
                const nodeDivisor = matrix.columns * matrix.rows;
                const num = node / nodeDivisor;
                const weight = lower + num * (upper - lower);
                matrix.set(i, j, weight);
            }
        }
    }

    public exportNodeWeights(): string {
        return JSON.stringify(this.hiddenWeightMatrix);
    }
}
