import type CubeStatus from '@/cube/CubeStatus';
import type SolutionStats from '@/cube/SolutionStats';
import type Cube from '@/cube/model/Cube';
import CubeController from '@/cube/CubeController';
import CubeEvaluation from '@/cube/CubeEvaluation';
import CubeDataPoints from '@/cube/CubeDataPoints';
import NeuralNetwork from '@/neuralnet/NeuralNetwork';

export default class CubePlayer {
    private static readonly MAX_MOVES: number = 100;

    public static solve(cube: Cube, neuralNetwork: NeuralNetwork, maxMovesOverride?: number): SolutionStats {
        const maxMoves = maxMovesOverride ? maxMovesOverride : CubePlayer.MAX_MOVES;
        let cubeStatus = CubeEvaluation.evaluate(cube, undefined);
        let solutionMoves = 0;
        let lastMove: number | undefined = -1;
        let sameMoveCount = 0;
        for (; solutionMoves < maxMoves; solutionMoves++) {
            if (cubeStatus.completed == 1 || sameMoveCount == 4) {
                break;
            }
            cubeStatus = CubePlayer.move(cube, neuralNetwork);
            sameMoveCount = cubeStatus.lastMove == lastMove ? sameMoveCount + 1 : 0;
            lastMove = cubeStatus.lastMove;
        }
        return {
            solutionMoves,
            cubeStatus,
        };
    }

    public static move(cube: Cube, neuralNetwork: NeuralNetwork): CubeStatus {
        const tuples = CubeDataPoints.tuples(cube);
        const action = neuralNetwork.evaluate(tuples).triggeredNode;
        CubeController.applyAction(cube, action);
        return CubeEvaluation.evaluate(cube, action);
    }
}
