import type CubeStatus from '@/cube/CubeStatus';
import type Cube from '@/cube/model/Cube';
import type Face from '@/cube/model/Face';

export default class CubeEvaluation {
    public static evaluate(cube: Cube, action?: number): CubeStatus {
        const faceColours: number[] = [];
        cube.faces.forEach((face: Face) => {
            faceColours.push([...new Set([...face.colours[0], ...face.colours[1], ...face.colours[2]])].length);
        });
        const completed: number = 6 / faceColours.reduce((acc, count) => acc + count, 0);
        return {
            faceColours,
            completed: completed,
            lastMove: action,
        };
    }
}
