import type Cube from '@/cube/model/Cube';
import Face from '@/cube/model/Face';
import { cloneFace, newFace } from '@/cube/model/FaceHelper';

export default class CubeHelper {
    public static newCube(faces?: Face[]): Cube {
        if (faces) {
            return {
                faces,
            };
        } else {
            const faces: Face[] = [];
            for (let i = 1; i <= 6; i++) {
                faces.push(newFace(i));
            }
            return {
                faces,
            };
        }
    }
    public static cloneCube(cube: Cube): Cube {
        const clonedFaces: Face[] = [];
        cube.faces.forEach((face: Face) => {
            clonedFaces.push(cloneFace(face));
        });
        return CubeHelper.newCube(clonedFaces);
    }

    public static rotateX1(cube: Cube) {
        const faceShift1 = cube.faces[0].colours[2];
        const faceShift3 = CubeHelper.getColumn(cube.faces[2], 0);
        const faceShift6 = cube.faces[5].colours[2];
        const faceShift5 = cube.faces[4].colours[2];
        cube.faces[0].colours[2] = faceShift3;
        CubeHelper.setColumn(cube.faces[2], 0, faceShift6);
        cube.faces[5].colours[2] = faceShift5;
        cube.faces[4].colours[2] = faceShift1;
        cube.faces[1].colours = CubeHelper.rotateAntiClockwise(cube.faces[1]).colours;
    }

    public static reverseRotateX1(cube: Cube) {
        const faceShift1 = cube.faces[0].colours[2];
        const faceShift3 = CubeHelper.getColumn(cube.faces[2], 0);
        const faceShift6 = cube.faces[5].colours[2];
        const faceShift5 = cube.faces[4].colours[2];
        cube.faces[0].colours[2] = faceShift5;
        CubeHelper.setColumn(cube.faces[2], 0, faceShift1);
        cube.faces[5].colours[2] = faceShift3;
        cube.faces[4].colours[2] = faceShift6;
        cube.faces[1].colours = CubeHelper.rotateClockwise(cube.faces[1]).colours;
    }

    public static rotateX3(cube: Cube) {
        const faceShift1 = cube.faces[0].colours[0];
        const faceShift3 = CubeHelper.getColumn(cube.faces[2], 2);
        const faceShift6 = cube.faces[5].colours[0];
        const faceShift5 = cube.faces[4].colours[0];
        cube.faces[0].colours[0] = faceShift3;
        CubeHelper.setColumn(cube.faces[2], 2, faceShift6);
        cube.faces[5].colours[0] = faceShift5;
        cube.faces[4].colours[0] = faceShift1;
        cube.faces[3].colours = CubeHelper.rotateClockwise(cube.faces[3]).colours;
    }

    public static reverseRotateX3(cube: Cube) {
        const faceShift1 = cube.faces[0].colours[0];
        const faceShift3 = CubeHelper.getColumn(cube.faces[2], 2);
        const faceShift6 = cube.faces[5].colours[0];
        const faceShift5 = cube.faces[4].colours[0];
        cube.faces[0].colours[0] = faceShift5;
        CubeHelper.setColumn(cube.faces[2], 2, faceShift1);
        cube.faces[5].colours[0] = faceShift3;
        cube.faces[4].colours[0] = faceShift6;
        cube.faces[3].colours = CubeHelper.rotateAntiClockwise(cube.faces[3]).colours;
    }

    public static rotateY1(cube: Cube) {
        const faceShift1 = CubeHelper.getColumn(cube.faces[0], 0).reverse();
        const faceShift2 = CubeHelper.getColumn(cube.faces[1], 0);
        const faceShift6 = CubeHelper.getColumn(cube.faces[5], 2).reverse();
        const faceShift4 = CubeHelper.getColumn(cube.faces[3], 2);
        CubeHelper.setColumn(cube.faces[0], 0, faceShift2);
        CubeHelper.setColumn(cube.faces[1], 0, faceShift6);
        CubeHelper.setColumn(cube.faces[5], 2, faceShift4);
        CubeHelper.setColumn(cube.faces[3], 2, faceShift1);
        cube.faces[4].colours = CubeHelper.rotateAntiClockwise(cube.faces[4]).colours;
    }

    public static reverseRotateY1(cube: Cube) {
        const faceShift1 = CubeHelper.getColumn(cube.faces[0], 0);
        const faceShift2 = CubeHelper.getColumn(cube.faces[1], 0).reverse();
        const faceShift6 = CubeHelper.getColumn(cube.faces[5], 2);
        const faceShift4 = CubeHelper.getColumn(cube.faces[3], 2).reverse();
        CubeHelper.setColumn(cube.faces[0], 0, faceShift4);
        CubeHelper.setColumn(cube.faces[1], 0, faceShift1);
        CubeHelper.setColumn(cube.faces[5], 2, faceShift2);
        CubeHelper.setColumn(cube.faces[3], 2, faceShift6);
        cube.faces[4].colours = CubeHelper.rotateClockwise(cube.faces[4]).colours;
    }

    public static rotateY3(cube: Cube) {
        const faceShift1 = CubeHelper.getColumn(cube.faces[0], 2).reverse();
        const faceShift2 = CubeHelper.getColumn(cube.faces[1], 2);
        const faceShift6 = CubeHelper.getColumn(cube.faces[5], 0).reverse();
        const faceShift4 = CubeHelper.getColumn(cube.faces[3], 0);
        CubeHelper.setColumn(cube.faces[0], 2, faceShift2);
        CubeHelper.setColumn(cube.faces[1], 2, faceShift6);
        CubeHelper.setColumn(cube.faces[5], 0, faceShift4);
        CubeHelper.setColumn(cube.faces[3], 0, faceShift1);
        cube.faces[2].colours = CubeHelper.rotateClockwise(cube.faces[2]).colours;
    }

    public static reverseRotateY3(cube: Cube) {
        const faceShift1 = CubeHelper.getColumn(cube.faces[0], 2);
        const faceShift2 = CubeHelper.getColumn(cube.faces[1], 2).reverse();
        const faceShift6 = CubeHelper.getColumn(cube.faces[5], 0);
        const faceShift4 = CubeHelper.getColumn(cube.faces[3], 0).reverse();
        CubeHelper.setColumn(cube.faces[0], 2, faceShift4);
        CubeHelper.setColumn(cube.faces[1], 2, faceShift1);
        CubeHelper.setColumn(cube.faces[5], 0, faceShift2);
        CubeHelper.setColumn(cube.faces[3], 0, faceShift6);
        cube.faces[2].colours = CubeHelper.rotateAntiClockwise(cube.faces[2]).colours;
    }

    public static rotateZ1(cube: Cube) {
        const faceShift2 = cube.faces[1].colours[0];
        const faceShift3 = cube.faces[2].colours[0];
        const faceShift4 = cube.faces[3].colours[0];
        const faceShift5 = CubeHelper.getColumn(cube.faces[4], 2);
        cube.faces[1].colours[0] = faceShift3;
        cube.faces[2].colours[0] = faceShift4;
        cube.faces[3].colours[0] = faceShift5;
        CubeHelper.setColumn(cube.faces[4], 2, faceShift2);
        cube.faces[0].colours = CubeHelper.rotateClockwise(cube.faces[0]).colours;
    }

    public static reverseRotateZ1(cube: Cube) {
        const faceShift2 = cube.faces[1].colours[0];
        const faceShift3 = cube.faces[2].colours[0];
        const faceShift4 = cube.faces[3].colours[0];
        const faceShift5 = CubeHelper.getColumn(cube.faces[4], 2);
        cube.faces[1].colours[0] = faceShift5;
        cube.faces[2].colours[0] = faceShift2;
        cube.faces[3].colours[0] = faceShift3;
        CubeHelper.setColumn(cube.faces[4], 2, faceShift4);
        cube.faces[0].colours = CubeHelper.rotateAntiClockwise(cube.faces[0]).colours;
    }

    public static rotateZ3(cube: Cube) {
        const faceShift2 = cube.faces[1].colours[2];
        const faceShift3 = cube.faces[2].colours[2];
        const faceShift4 = cube.faces[3].colours[2];
        const faceShift5 = CubeHelper.getColumn(cube.faces[4], 0);
        cube.faces[1].colours[2] = faceShift3;
        cube.faces[2].colours[2] = faceShift4;
        cube.faces[3].colours[2] = faceShift5;
        CubeHelper.setColumn(cube.faces[4], 0, faceShift2);
        cube.faces[5].colours = CubeHelper.rotateAntiClockwise(cube.faces[5]).colours;
    }

    public static reverseRotateZ3(cube: Cube) {
        const faceShift2 = cube.faces[1].colours[2];
        const faceShift3 = cube.faces[2].colours[2];
        const faceShift4 = cube.faces[3].colours[2];
        const faceShift5 = CubeHelper.getColumn(cube.faces[4], 0);
        cube.faces[1].colours[2] = faceShift5;
        cube.faces[2].colours[2] = faceShift2;
        cube.faces[3].colours[2] = faceShift3;
        CubeHelper.setColumn(cube.faces[4], 0, faceShift4);
        cube.faces[5].colours = CubeHelper.rotateClockwise(cube.faces[5]).colours;
    }

    private static rotateAntiClockwise(face: Face) {
        const rotatedFace = newFace();
        rotatedFace.colours = [
            CubeHelper.getColumn(face, 2),
            CubeHelper.getColumn(face, 1),
            CubeHelper.getColumn(face, 0),
        ];
        return rotatedFace;
    }

    private static rotateClockwise(face: Face) {
        const rotatedFace = newFace();
        rotatedFace.colours = [
            CubeHelper.getColumn(face, 0).reverse(),
            CubeHelper.getColumn(face, 1).reverse(),
            CubeHelper.getColumn(face, 2).reverse(),
        ];
        return rotatedFace;
    }

    private static getColumn(face: Face, index: number): number[] {
        return [face.colours[0][index], face.colours[1][index], face.colours[2][index]];
    }

    private static setColumn(face: Face, index: number, colours: number[]) {
        for (let i = 0; i < 3; i++) {
            face.colours[i][index] = colours[i];
        }
    }
}

export const {
    newCube,
    cloneCube,
    rotateX1,
    rotateX3,
    rotateY1,
    rotateY3,
    rotateZ3,
    rotateZ1,
    reverseRotateX1,
    reverseRotateX3,
    reverseRotateY1,
    reverseRotateY3,
    reverseRotateZ1,
    reverseRotateZ3,
} = CubeHelper;
