import type Cube from '@/cube/model/Cube';
import type Face from '@/cube/model/Face';
import type Tile from '@/ui/Tile';
import { Matrix } from 'ml-matrix';
import { PLANE_X1, PLANE_X3, PLANE_Y1, PLANE_Y3, PLANE_Z1, PLANE_Z3 } from '@/cube/Move';

export default class TileBlock {
    private readonly BLOCK_SIZE = 48;
    private readonly VIEW_DISTANCE = 256;
    private readonly PERSPECTIVE_DISTANCE = 360;
    private readonly PLANE_BLOCK_IDS: number[][] = [
        [1, 2, 3, 4, 5, 6, 7, 8, 9],
        [21, 20, 19, 24, 23, 22, 27, 26, 25],
        [25, 22, 19, 16, 13, 10, 7, 4, 1],
        [3, 12, 21, 6, 15, 24, 9, 18, 27],
        [19, 20, 21, 10, 11, 12, 1, 2, 3],
        [27, 26, 25, 18, 17, 16, 9, 8, 7],
    ];

    private readonly blockNumber: number;
    private readonly faceColour: number[];
    private readonly x: number;
    private readonly y: number;
    private readonly z: number;
    private rotateX: number;
    private rotateY: number;
    private rotateZ: number;
    private planeRotateX: number;
    private planeRotateY: number;
    private planeRotateZ: number;

    constructor(blockNumber: number, cube: Cube) {
        this.blockNumber = blockNumber;
        this.x = ((blockNumber - 1) % 3) - 1.5;
        this.y = Math.floor(((blockNumber - 1) % 9) / 3) - 1.5;
        this.z = 0.5 - Math.floor((blockNumber - 1) / 9);
        this.faceColour = this.getFaceColours(cube);
        this.rotateX = 0;
        this.rotateY = 0;
        this.rotateZ = 0;
        this.planeRotateX = 0;
        this.planeRotateY = 0;
        this.planeRotateZ = 0;
    }

    private getFaceColours(cube: Cube) {
        return [this.col1(cube), this.col2(cube), this.col3(cube), this.col4(cube), this.col5(cube), this.col6(cube)];
    }

    private colour(blockIds: number[], face: Face) {
        if (blockIds.indexOf(this.blockNumber) >= 0) {
            const offsetRow = Math.floor(blockIds.indexOf(this.blockNumber) / 3);
            const offsetCol = blockIds.indexOf(this.blockNumber) % 3;
            return face.colours[offsetRow][offsetCol];
        }
        return 0;
    }

    private col1(cube: Cube): number {
        return this.colour(this.PLANE_BLOCK_IDS[PLANE_Z1], cube.faces[0]);
    }

    private col2(cube: Cube): number {
        return this.colour(this.PLANE_BLOCK_IDS[PLANE_X1], cube.faces[1]);
    }

    private col3(cube: Cube): number {
        return this.colour(this.PLANE_BLOCK_IDS[PLANE_Y3], cube.faces[2]);
    }

    private col4(cube: Cube): number {
        return this.colour(this.PLANE_BLOCK_IDS[PLANE_X3], cube.faces[3]);
    }

    private col5(cube: Cube): number {
        return this.colour(this.PLANE_BLOCK_IDS[PLANE_Y1], cube.faces[4]);
    }

    private col6(cube: Cube): number {
        return this.colour(this.PLANE_BLOCK_IDS[PLANE_Z3], cube.faces[5]);
    }

    private getVectors(): Matrix[] {
        const face1: Matrix = new Matrix([
            [this.x, this.y, this.z],
            [this.x, this.y, this.z + 1],
            [this.x + 1, this.y, this.z + 1],
            [this.x + 1, this.y, this.z],
        ]);
        const face2: Matrix = new Matrix([
            [this.x, this.y, this.z + 1],
            [this.x, this.y + 1, this.z + 1],
            [this.x + 1, this.y + 1, this.z + 1],
            [this.x + 1, this.y, this.z + 1],
        ]);
        const face3: Matrix = new Matrix([
            [this.x + 1, this.y, this.z + 1],
            [this.x + 1, this.y + 1, this.z + 1],
            [this.x + 1, this.y + 1, this.z],
            [this.x + 1, this.y, this.z],
        ]);
        const face4: Matrix = new Matrix([
            [this.x, this.y, this.z],
            [this.x, this.y + 1, this.z],
            [this.x + 1, this.y + 1, this.z],
            [this.x + 1, this.y, this.z],
        ]);
        const face5: Matrix = new Matrix([
            [this.x, this.y, this.z + 1],
            [this.x, this.y + 1, this.z + 1],
            [this.x, this.y + 1, this.z],
            [this.x, this.y, this.z],
        ]);
        const face6: Matrix = new Matrix([
            [this.x, this.y + 1, this.z],
            [this.x, this.y + 1, this.z + 1],
            [this.x + 1, this.y + 1, this.z + 1],
            [this.x + 1, this.y + 1, this.z],
        ]);
        return [face1, face2, face3, face4, face5, face6];
    }

    public getView(): Tile[] {
        const vectors = this.getVectors();
        return vectors.map((m, index) => {
            let v = this.transformXRotation(this.planeRotateX, m);
            v = this.transformYRotation(this.planeRotateY, v);
            v = this.transformZRotation(this.planeRotateZ, v);
            v = this.transformXRotation(this.rotateX, v);
            v = this.transformYRotation(this.rotateY, v);
            v = this.transformZRotation(this.rotateZ, v);
            v.multiply(this.BLOCK_SIZE);
            const dist: number = this.distance(v);
            this.applyPerspective(v);
            v.round();
            const x1 = v.get(0, 0);
            const y1 = v.get(0, 1);
            const x2 = v.get(1, 0);
            const y2 = v.get(1, 1);
            const x3 = v.get(2, 0);
            const y3 = v.get(2, 1);
            const x4 = v.get(3, 0);
            const y4 = v.get(3, 1);
            return { x1, y1, x2, y2, x3, y3, x4, y4, dist, colour: this.faceColour[index] };
        });
    }

    private transformXRotation(angle: number, matrix: Matrix) {
        return new Matrix([
            [1, 0, 0],
            [0, Math.cos(angle), -Math.sin(angle)],
            [0, Math.sin(angle), Math.cos(angle)],
        ])
            .mmul(matrix.transpose())
            .transpose();
    }

    private transformYRotation(angle: number, matrix: Matrix) {
        return new Matrix([
            [Math.cos(angle), 0, Math.sin(angle)],
            [0, 1, 0],
            [-Math.sin(angle), 0, Math.cos(angle)],
        ])
            .mmul(matrix.transpose())
            .transpose();
    }

    private transformZRotation(angle: number, matrix: Matrix) {
        return new Matrix([
            [Math.cos(angle), -Math.sin(angle), 0],
            [Math.sin(angle), Math.cos(angle), 0],
            [0, 0, 1],
        ])
            .mmul(matrix.transpose())
            .transpose();
    }

    private distance(v: Matrix) {
        const center = v.getColumn(2);
        const z1 = this.VIEW_DISTANCE - center[0];
        const z2 = this.VIEW_DISTANCE - center[1];
        const z3 = this.VIEW_DISTANCE - center[2];
        const z4 = this.VIEW_DISTANCE - center[3];
        return Math.floor(Math.sqrt(z1 * z1 + z2 * z2 + z3 * z3 + z4 * z4));
    }

    withRotateX(degrees: number): TileBlock {
        this.rotateX = (degrees * Math.PI) / 180;
        return this;
    }

    withRotateY(degrees: number): TileBlock {
        this.rotateY = (degrees * Math.PI) / 180;
        return this;
    }

    withRotateZ(degrees: number): TileBlock {
        this.rotateZ = (degrees * Math.PI) / 180;
        return this;
    }

    private rotatePlaneX(degrees: number): TileBlock {
        this.planeRotateX = (degrees * Math.PI) / 180;
        return this;
    }

    private rotatePlaneY(degrees: number): TileBlock {
        this.planeRotateY = (degrees * Math.PI) / 180;
        return this;
    }

    private rotatePlaneZ(degrees: number): TileBlock {
        this.planeRotateZ = (degrees * Math.PI) / 180;
        return this;
    }

    withRotation(plane: number | undefined, degrees: number) {
        switch (plane) {
            case PLANE_X1:
                if (this.PLANE_BLOCK_IDS[PLANE_X1].indexOf(this.blockNumber) >= 0) {
                    this.rotatePlaneZ(degrees);
                }
                break;
            case PLANE_X3:
                if (this.PLANE_BLOCK_IDS[PLANE_X3].indexOf(this.blockNumber) >= 0) {
                    this.rotatePlaneZ(degrees);
                }
                break;
            case PLANE_Y1:
                if (this.PLANE_BLOCK_IDS[PLANE_Y1].indexOf(this.blockNumber) >= 0) {
                    this.rotatePlaneX(-degrees);
                }
                break;
            case PLANE_Y3:
                if (this.PLANE_BLOCK_IDS[PLANE_Y3].indexOf(this.blockNumber) >= 0) {
                    this.rotatePlaneX(-degrees);
                }
                break;
            case PLANE_Z1:
                if (this.PLANE_BLOCK_IDS[PLANE_Z1].indexOf(this.blockNumber) >= 0) {
                    this.rotatePlaneY(degrees);
                }
                break;
            case PLANE_Z3:
                if (this.PLANE_BLOCK_IDS[PLANE_Z3].indexOf(this.blockNumber) >= 0) {
                    this.rotatePlaneY(degrees);
                }
                break;
        }
        return this;
    }

    private applyPerspective(matrix: Matrix) {
        for (let i = 0; i < matrix.rows; i++) {
            const zd = this.PERSPECTIVE_DISTANCE - matrix.getColumnVector(2).get(i, 0);
            matrix.set(i, 0, (this.PERSPECTIVE_DISTANCE * matrix.get(i, 0)) / zd);
            matrix.set(i, 1, (this.PERSPECTIVE_DISTANCE * matrix.get(i, 1)) / zd);
        }
    }
}
