import type { JSX, MouseEvent } from 'react';
import React from 'react';
import { Col, Container, Row } from 'reactstrap';
import { RootState } from '@/state/Store';
import { rotateX, rotateY, transitionStep } from '@/state/CubeSlice';
import { connect, ConnectedProps } from 'react-redux';
import TileBlock from '@/ui/TileBlock';
import Tile from '@/ui/Tile';

interface View3DState {
    rotate: boolean;
}

export class View3D extends React.Component<PropsFromRedux, View3DState> {
    private readonly WINDOW_HEIGHT = 300;
    private readonly WINDOW_WIDTH = 300;
    private readonly MIN_ANGLE = 0;
    private readonly MAX_ANGLE = 360;
    private readonly COLOURS: string[] = ['black', 'yellow', 'blue', 'red', 'lime', 'orange', 'white'];
    private intervalId: NodeJS.Timeout | undefined = undefined;

    constructor(props: PropsFromRedux) {
        super(props);
        this.state = {
            rotate: false,
        };
    }

    fireEvent() {
        if (this.props.transition > 0) {
            this.props.transitionStep();
        }
        this.startTimer();
    }

    componentDidMount() {
        this.startTimer();
    }

    componentWillUnmount() {
        this.stopTimer();
    }

    startTimer() {
        this.intervalId = setTimeout(() => this.fireEvent(), 50);
    }

    stopTimer() {
        if (this.intervalId) {
            clearInterval(this.intervalId);
            this.intervalId = undefined;
        }
    }

    private renderBackground() {
        return [
            <rect
                data-testid="background"
                key={`background`}
                x={this.WINDOW_WIDTH / -2}
                y={this.WINDOW_HEIGHT / -2}
                width="100%"
                height="100%"
                fill="#000040"
            />,
        ];
    }

    private renderCube(): JSX.Element[] {
        const tiles: Tile[] = this.tiles();
        return tiles.map((tile, index): JSX.Element => {
            return (
                <polygon
                    key={`tile-${index}`}
                    points={`${tile.x1},${tile.y1} ${tile.x2},${tile.y2} ${tile.x3},${tile.y3} ${tile.x4},${tile.y4}`}
                    fill={`${this.COLOURS[tile.colour]}`}
                    stroke={`${this.COLOURS[0]}`}
                    strokeWidth={3}
                />
            );
        });
    }

    private renderMoves(): JSX.Element {
        return (
            <text
                data-testid="moves"
                key="moves"
                x={this.WINDOW_WIDTH / -2}
                y={this.WINDOW_HEIGHT / -2}
                dominantBaseline="hanging"
                fill="white"
                fontWeight="bold"
                fontSize="20px"
                fontFamily="Monospace"
            >
                MOVES: {this.props.moves}
            </text>
        );
    }

    private renderState(): JSX.Element {
        const status = this.props.complete ? 'COMPLETE' : this.props.sameMoveCount > 3 ? 'FAILED' : '';
        return (
            <text
                data-testid="state"
                key="state"
                x={-15 * status.length}
                y={-25}
                dominantBaseline="hanging"
                fill="white"
                fontWeight="bold"
                fontSize="50px"
                fontFamily="Monospace"
                stroke="black"
                strokeWidth={2}
                transform="rotate(-15)"
            >
                {status}
            </text>
        );
    }

    private tiles(): Tile[] {
        const tiles = [];
        for (let i = 1; i <= 27; i++) {
            tiles.push(
                ...new TileBlock(i, this.props.cube)
                    .withRotateX(this.props.xRotation)
                    .withRotateY(this.props.yRotation)
                    .withRotation(this.props.lastMove, this.props.transition)
                    .getView(),
            );
        }
        return tiles.sort((a, b) => b.dist - a.dist);
    }

    rotate(event: MouseEvent) {
        if (this.state.rotate) {
            const rotationX = (this.MAX_ANGLE + this.props.xRotation - event.movementY) % this.MAX_ANGLE;
            const rotationY = (this.MAX_ANGLE + this.props.yRotation + event.movementX) % this.MAX_ANGLE;
            this.props.rotateX(rotationX);
            this.props.rotateY(rotationY);
        }
    }

    startRotate() {
        this.setState({ rotate: true });
    }

    stopRotate() {
        this.setState({ rotate: false });
    }

    renderView() {
        const cubeView: JSX.Element[] = [
            ...this.renderBackground(),
            ...this.renderCube(),
            this.renderMoves(),
            this.renderState(),
        ];
        const viewBox = `${this.WINDOW_WIDTH / -2} ${this.WINDOW_HEIGHT / -2} 
            ${this.WINDOW_WIDTH} ${this.WINDOW_HEIGHT}`;
        return (
            <Col
                data-testid="cubeDiv"
                lg={{ offset: 1, size: 10 }}
                onMouseDown={this.startRotate.bind(this)}
                onMouseUp={this.stopRotate.bind(this)}
                onMouseLeave={this.stopRotate.bind(this)}
                onMouseMove={this.rotate.bind(this)}
            >
                <svg key="3dCube" xmlns="http://www.w3.org/2000/svg" viewBox={viewBox}>
                    {cubeView}
                    Sorry, your browser does not support inline SVG.
                </svg>
            </Col>
        );
    }

    render() {
        return (
            <Container fluid={true} data-testid="3dView">
                <Row className="vcenter mx-3">{this.renderView()}</Row>
            </Container>
        );
    }
}

const mapStateToProps = (state: RootState) => ({
    cube: state.cube.cube,
    xRotation: state.cube.xRotation,
    yRotation: state.cube.yRotation,
    moves: state.cube.moves,
    lastMove: state.cube.lastMove,
    transition: state.cube.transition,
    complete: state.cube.complete,
    sameMoveCount: state.cube.sameMoveCount,
});

const connector = connect(mapStateToProps, { transitionStep, rotateX, rotateY });
type PropsFromRedux = ConnectedProps<typeof connector>;

export default connector(View3D);
